<template>
	<view class="container">
		<!-- 周日历 -->
		<view class="calendar-container">
			<view class="week-header">
				<view class="week-day" v-for="(day, index) in weekDays" :key="index">
					{{ day }}
				</view>
			</view>
			<view class="week-dates">
				<view class="date-item" :class="{ active: selectedDate === item.date }"
					v-for="(item, index) in weekDates" :key="index" @click="selectDate(item)">
					{{ item.day }}
				</view>
			</view>
		</view>

		<!-- 我的预约列表 -->
		<view class="appointment-list">
			<view class="list-title">我的预约</view>
			<scroll-view class="list-content" scroll-y>
				<view class="appointment-wrapper" v-for="(item, index) in appointmentList" :key="index">
					<view class="appointment-item" @touchstart="touchStart($event, index)"
						@touchmove="touchMove($event, index)" @touchend="touchEnd($event, index)"
						:style="{ transform: `translateX(${item.translateX || 0}rpx)` }">
						<view class="item-date">{{ item.date }}</view>
						<view class="item-time">{{ item.timeSlot }}</view>
						<view class="item-status" :class="item.status">{{ getStatusText(item.status) }}</view>
					</view>
					<view class="delete-btn" @click="showCancelDialog(index)">
						取消
					</view>
				</view>
				<view v-if="appointmentList.length === 0" class="empty-tip">
					暂无预约记录
				</view>
			</scroll-view>
		</view>

		<!-- 时间段选择弹窗 -->
		<view class="popup-mask" v-if="showTimePopup" @click="closePopup">
			<view class="time-popup" @click.stop>
				<view class="popup-header">
					<text class="popup-title">选择预约时间</text>
					<text class="popup-date">{{ selectedDateText }}</text>
				</view>

				<!-- 上午时间段 -->
				<view class="time-section">
					<view class="section-title">上午</view>
					<view class="time-grid">
						<view class="time-slot" :class="{
							booked: slot.isBooked,
							available: !slot.isBooked,
							selected: selectedTimeSlot === slot.time
						}" v-for="(slot, index) in morningSlots" :key="index" @click="selectTimeSlot(slot)">
							<text class="slot-time">{{ slot.time }}</text>
							<text class="slot-status">{{ slot.isBooked ? '已预约' : '可预约' }}</text>
						</view>
					</view>
				</view>

				<!-- 下午时间段 -->
				<view class="time-section">
					<view class="section-title">下午</view>
					<view class="time-grid">
						<view class="time-slot" :class="{
							booked: slot.isBooked,
							available: !slot.isBooked,
							selected: selectedTimeSlot === slot.time
						}" v-for="(slot, index) in afternoonSlots" :key="index" @click="selectTimeSlot(slot)">
							<text class="slot-time">{{ slot.time }}</text>
							<text class="slot-status">{{ slot.isBooked ? '已预约' : '可预约' }}</text>
						</view>
					</view>
				</view>

				<!-- 弹窗按钮 -->
				<view class="popup-buttons">
					<button class="btn-cancel" @click="closePopup">取消</button>
					<button class="btn-confirm" @click="confirmAppointment" :disabled="!selectedTimeSlot">确定</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			weekDays: ['今日', '周六', '周日', '周一', '周二', '周三', '周四'],
			weekDates: [],
			selectedDate: '',
			selectedDateText: '',
			selectedTimeSlot: '',
			showTimePopup: false,
			startX: 0,
			startTime: 0,
			morningSlots: [
				{ time: '09:00', isBooked: false },
				{ time: '11:00', isBooked: true },
			],
			afternoonSlots: [

				{ time: '14:00', isBooked: false },
				{ time: '16:00', isBooked: true },
			],
			appointmentList: [
				{ date: '2024-01-03', timeSlot: '9:00-9:30', status: 'confirmed' },
				{ date: '2024-01-05', timeSlot: '14:00-14:30', status: 'pending' },
				{ date: '2024-01-08', timeSlot: '10:30-11:00', status: 'cancelled' }
			]
		}
	},
	onShow() {
		console.log("预约列表：", this.appointmentList)
		this.initWeekDates()
	},
	methods: {
		// 初始化一周日期
		initWeekDates() {
			const today = new Date()
			const weekDates = []

			for (let i = 0; i < 7; i++) {
				const date = new Date(today)
				date.setDate(today.getDate() + i)

				weekDates.push({
					day: date.getDate(),
					date: this.formatDate(date),
					fullDate: date
				})
			}

			this.weekDates = weekDates
			this.selectedDate = weekDates[0].date
			console.log("选中日期：", this.selectedDate)
		},

		// 格式化日期
		formatDate(date) {
			const year = date.getFullYear()
			const month = String(date.getMonth() + 1).padStart(2, '0')
			const day = String(date.getDate()).padStart(2, '0')
			return `${year}-${month}-${day}`
		},

		// 选择日期
		selectDate(dateItem) {
			this.selectedDate = dateItem.date
			this.selectedDateText = dateItem.date
			this.selectedTimeSlot = ''

			// 打开时间选择弹窗
			this.showTimePopup = true
		},

		// 选择时间段
		selectTimeSlot(slot) {
			if (slot.isBooked) {
				uni.showToast({
					title: '该时间段已被预约',
					icon: 'none'
				})
				return
			}

			this.selectedTimeSlot = slot.time
		},

		// 确认预约
		confirmAppointment() {
			if (!this.selectedTimeSlot) {
				uni.showToast({
					title: '请选择时间段',
					icon: 'none'
				})
				return
			}

			uni.showModal({
				title: '确认预约',
				content: `确认预约时间为：\r\n${this.selectedDateText} ${this.selectedTimeSlot}`,
				success: (res) => {
					if (res.confirm) {
						// 这里调用预约接口
						this.submitAppointment()
					}
				}
			})
		},

		// 提交预约（逻辑待实现）
		submitAppointment() {
			uni.showLoading({ title: '预约中...' })

			// 模拟API调用
			setTimeout(() => {
				uni.hideLoading()
				uni.showToast({
					title: '预约成功',
					icon: 'success'
				})

				// 更新预约列表
				this.appointmentList.unshift({
					date: this.selectedDateText,
					timeSlot: `${this.selectedTimeSlot}-${this.getEndTime(this.selectedTimeSlot)}`,
					status: 'confirmed'
				})

				this.closePopup()
			}, 1500)
		},

		// 获取结束时间（简单示例）
		getEndTime(startTime) {
			const [hour, minute] = startTime.split(':')
			const endMinute = parseInt(minute) + 30
			if (endMinute >= 60) {
				return `${parseInt(hour) + 1}:${String(endMinute - 60).padStart(2, '0')}`
			}
			return `${hour}:${String(endMinute).padStart(2, '0')}`
		},

		// 关闭弹窗
		closePopup() {
			this.showTimePopup = false
			this.selectedTimeSlot = ''
		},

		// 获取状态文本
		getStatusText(status) {
			const statusMap = {
				confirmed: '已确认',
				pending: '待确认',
				cancelled: '已取消'
			}
			return statusMap[status] || '未知'
		},
		touchStart(e, index) {
			// 重置其他项的位置
			this.appointmentList.forEach((item, i) => {
				if (i !== index) {
					this.$set(this.appointmentList[i], 'translateX', 0)
				}
			})
			this.startX = e.touches[0].clientX
			this.startTime = Date.now()
		},

		touchMove(e, index) {
			const currentX = e.touches[0].clientX
			const deltaX = this.startX - currentX

			// 允许左右滑动
			const translateX = Math.max(Math.min(deltaX, 160), 0) // 限制滑动范围
			this.$set(this.appointmentList[index], 'translateX', -translateX)
		},

		touchEnd(e, index) {
			const currentTranslateX = this.appointmentList[index].translateX || 0

			// 如果滑动距离超过80rpx，显示删除按钮
			if (Math.abs(currentTranslateX) > 80) {
				this.$set(this.appointmentList[index], 'translateX', -160)
			} else {
				this.$set(this.appointmentList[index], 'translateX', 0)
			}
		},

		// 显示取消确认对话框
		showCancelDialog(index) {
			uni.showModal({
				title: '确认取消',
				content: '确定要取消这个预约吗？',
				success: (res) => {
					if (res.confirm) {
						this.cancelAppointment(index)
					} else {
						// 取消时恢复原位
						this.$set(this.appointmentList[index], 'translateX', 0)
					}
				}
			})
		},

		// 取消预约
		cancelAppointment(index) {
			uni.showLoading({ title: '取消中...' })

			// 模拟API调用
			setTimeout(() => {
				uni.hideLoading()
				uni.showToast({
					title: '取消成功',
					icon: 'success'
				})

				// 从列表中移除
				this.appointmentList.splice(index, 1)
			}, 1000)
		}
	}
}
</script>

